import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return{
            marriage: null,
            bear: null,
            list1: null,
            list2: null,
            patient: this.$store.state.patient
        }
    },
    created(){
        this.pull()
    },
    methods: {
        pull(){
            this.$post('PlatFormAPI/Category/QueryCategoryItem',{
                CategoryIds: [ 2, 47 ]
            }).then(o => {
                const info = this.$store.state.Health
                o.forEach(v => {
                    if(v.CategoryId == 2){
                        this.list1 = v.Items
                    }
                    else if(v.CategoryId == 47){
                        this.list2 = v.Items
                    }
                })
                this.list1.forEach(v => {
                    if(info){
                        if(info.MaritalStatus == v.CategoryItemValue){
                            this.marriage = v
                        }
                    }
                })
                this.list2.forEach(v => {
                    if(info){
                        if(info.ReproductiveStatus == v.CategoryItemValue){
                            this.bear = v
                        }
                    }
                })
            })
        },
        send(){
            this.$post('PlatFormAPI/PatientAccount/SaveHealthInfo',{
                SaveType: 2,
                PatientId: this.patient.PatientId,
                ReproductiveStatus: this.bear ? this.bear.CategoryItemValue : 0,
                MaritalStatus: this.marriage ? this.marriage.CategoryItemValue : 0
            }).then(o => {
            	this.$store.dispatch('pushIsFresh',true)
                this.$toast('保存成功')
                this.$router.back()
            })
        }
    }
}



